.logo img{
  width: 50px;
  height: 50px;
}



@media (prefers-reduced-motion: no-preference) {
  .logo img {
    animation: App-logo-swing 1s infinite alternate ease-in-out;
  }
}

.header{
  padding-inline: 10vh;
}

.App_header {
  text-align: center;
  background-color: rgb(200, 193, 193);
  min-height: 10vh;
  display: flex;
 
  align-items: center;
  justify-content: start;
  justify-content: space-between;
  padding-inline: 10vh;
  font-size: calc(5px + 2vmin);
  color: black;
}


@keyframes App-logo-swing {
  0% {
    transform: rotate(15deg);
  }
  50% {
    transform: rotate(-15deg);
  }
  100% {
    transform: rotate(15deg);
  }
}
